import React, { useContext, useEffect, useState } from 'react'
import { AppContext, UserContext } from '../App'
import DepositTabs from '../components/DepositTabs'
import Footer from '../components/Footer'
import Header from '../components/Header'
import MenuNotification from '../components/MenuNotification'
import MenuSidebar from '../components/MenuSidebar'
import { getDepositTypes } from '../services'

function Deposit() {
    const mode = useContext(UserContext)
    const [state, setState] = useContext(AppContext)
    useEffect(() => {
        getDepositTypes().then(res => {
            console.log(res.data)
        })
    }, [])
    return (
        <div className='t1'>
            <div className='page'>
                <div class="page-content footer-clear">
                    <Header />
                    <div class="card card-fixed">
                        <div class="card-center mx-2 px-2 p-0 pt-3 rounded-m">
                            <DepositTabs />
                            <div className="d-flex justify-content-end">
                                <button class="btn btn-xl gradient-highlight shadow-bg shadow-bg-s mt-4 ms-auto">Deposit</button>
                            </div>
                            <div class="d-flex align-items-center flex-column my-5">
                                <i class="fa-brands fa-whatsapp color-dark-green fa-3x" style={{ color: "#25D366" }}></i>
                                <h3>Live chat support</h3>
                            </div>
                        </div>
                        {mode == "light-mode" && <div class={`card-overlay rounded-0 m-0 bg-black-light opacity-70`}></div>}
                    </div>
                </div>
                <Footer />
                <MenuNotification />
                <MenuSidebar />
            </div>
        </div>
    )
}

export default Deposit