import "./App.css"
import React, { useEffect, useState } from 'react';
import { BrowserRouter as Router, Routes, Route, Outlet, Link, useLocation, useNavigate } from "react-router-dom";
// import { ToastContainer } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';
import Home from './pages/Home';
import SignIn from './pages/SignIn';
import SignUp from './pages/SignUp';
import SignUp2 from "./pages/SignUp2";
import SignUp3 from "./pages/SignUp3";
import Notifications from "./pages/Notifications";
import Transactions from "./pages/TransactionHistory";
import Bets from "./pages/BetHistory";
import Account from "./pages/Account";
import Deposit from "./pages/Deposit";
import Withdraw from "./pages/Withdraw";
import Promotions from "./pages/Promotions";
import { ToastContainer } from "react-toastify";
import Protected from "./ProtectedRoute";
import PublicRoute from "./PublicRoute";
export const UserContext = React.createContext();
export const AppContext = React.createContext();


function App() {
  const location = useLocation()
  const navigate = useNavigate()
  const [mode, setMode] = useState(localStorage.getItem("PayApp-Theme"))
  const [appData, setAppData] = useState({
    signupStep: 0,
    refreshToken: false,
    accessToken: false,
    promotions:[],
    categories: [],
    categoriesDetails: [],
    categoryLoading: false,
    wallet: {
      balance: "0.00",
      currency: "",
      id: "",
      user: "",
      username: "Username"
    }
  })

  useEffect(() => {
    window.addEventListener('storage', () => {
      setMode(localStorage.getItem("PayApp-Theme"))
    })
    window.addEventListener("resize", card_extender);
    const signupId = localStorage.getItem("signupId")
    const signupStep = localStorage.getItem("signupStep")
    const refreshToken = localStorage.getItem("refreshToken")
    const accessToken = localStorage.getItem("accessToken")
    setAppData({
      ...appData,
      accessToken,
      refreshToken,
      signupId,
      signupStep: signupStep ? signupStep : 0,
    })
  }, [])

  useEffect(() => {
    if (appData.signupStep == 1) {
      console.log('signupStep', appData.signupStep)
      navigate("/signup2")
    } else if (appData.signupStep == 2) {
      console.log('signupStep', appData.signupStep)
      navigate("/signup3")
    }
  }, [appData.signupStep])

  function card_extender() {
    const cards = document.getElementsByClassName('card');
    for (let i = 0; i < cards.length; i++) {
      if (cards[i].getAttribute('data-card-height') === "cover") {
        if (window.matchMedia('(display-mode: fullscreen)').matches) { var windowHeight = window.outerHeight; }
        if (!window.matchMedia('(display-mode: fullscreen)').matches) { var windowHeight = window.innerHeight; }
        var coverHeight = windowHeight + 'px';
      }
      if (cards[i].getAttribute('data-card-height') === "cover-card") {
        var windowHeight = window.innerHeight;
        var coverHeight = windowHeight - 200 + 'px';
        cards[i].style.height = coverHeight
      }
      if (cards[i].getAttribute('data-card-height') === "cover-full") {
        if (window.matchMedia('(display-mode: fullscreen)').matches) { var windowHeight = window.outerHeight; }
        if (!window.matchMedia('(display-mode: fullscreen)').matches) { var windowHeight = window.innerHeight; }
        var coverHeight = windowHeight + 'px';
        cards[i].style.height = coverHeight
      }
      if (cards[i].hasAttribute('data-card-height')) {
        var getHeight = cards[i].getAttribute('data-card-height');
        cards[i].style.height = getHeight + 'px';
        if (getHeight === "cover") {
          var totalHeight = getHeight
          cards[i].style.height = coverHeight
        }
      }
    }
  }

  useEffect(() => {
    card_extender()
  }, [location])


  return (
    <UserContext.Provider value={mode}>
      <AppContext.Provider value={[appData, setAppData]}>
        <ToastContainer />
        <Routes>
          <Route path="/" element={
            <PublicRoute isSignedIn={appData.accessToken}>
              <SignIn />
            </PublicRoute>
          } />
          <Route exact path="/signup" element={
            <PublicRoute isSignedIn={appData.accessToken}>
              <SignUp />
            </PublicRoute>
          } />
          <Route exact path="/signup2" element={
            <PublicRoute isSignedIn={appData.accessToken}>
              <SignUp2 />
            </PublicRoute>
          } />
          <Route exact path="/signup3" element={
            <PublicRoute isSignedIn={appData.accessToken}>
              <SignUp3 />
            </PublicRoute>
          } />
          <Route exact path="/home" element={
            <Protected isSignedIn={appData.accessToken}>
              <Home />
            </Protected>
          } />
          <Route exact path="/notifications" element={
            <Protected isSignedIn={appData.accessToken}>
              <Notifications />
            </Protected>
          } />
          <Route exact path="/transactions" element={
            <Protected isSignedIn={appData.accessToken}>
              <Transactions />
            </Protected>
          } />
          <Route exact path="/bet-history" element={
            <Protected isSignedIn={appData.accessToken}>
              <Bets />
            </Protected>
          } />
          <Route exact path="/account" element={
            <Protected isSignedIn={appData.accessToken}>
              <Account />
            </Protected>
          } />
          <Route exact path="/deposit" element={
            <Protected isSignedIn={appData.accessToken}>
              <Deposit />
            </Protected>
          } />
          <Route exact path="/withdraw" element={
            <Protected isSignedIn={appData.accessToken}>
              <Withdraw />
            </Protected>
          } />
          <Route exact path="/promotions" element={
            <Protected isSignedIn={appData.accessToken}>
              <Promotions />
            </Protected>
          } />
          {/* <Route path="*" element={<NoMatch />} /> */}
        </Routes>
      </AppContext.Provider>
    </UserContext.Provider>
  );
}

export default App;
