import React, { useContext, useState } from 'react'
import { useNavigate } from 'react-router-dom'
import { AppContext } from '../App'
import { signup } from '../services'
import { toast } from 'react-toastify'

function SignUp() {
    const navigate = useNavigate()
    const [state, setState] = useContext(AppContext)
    const [data, setData] = useState({
        username: "",
        password: "",
        password2: "",
        mobile: "",
    })

    const handleSubmit = () => {
        const { username, password, password2, mobile } = data
        if (username && password && password2 && mobile) {
            signup({
                ...data,
                cc: "60",
            }).then(res => {
                if (res.data && res.data.id) {
                    localStorage.setItem("signupId", res.data.id)
                    localStorage.setItem("signupStep", 1)
                    setState({
                        ...state,
                        signupStep: 1,
                        signupId: res.data.id
                    })
                    navigate("/signup2")
                } else {
                    toast.error("Something went wrong!")
                }
            }).catch(err => {
                toast.error("Something went wrong!")
            })
        } else {
            toast.error("All fields are mandatory")
        }
    }
    return (
        <div className="t3" >
            <div id="page">
                <div class="page-content pb-0">
                    <div data-card-height="cover-full" class="card mb-0">
                        <div class="card-center">
                            <div class="text-center">
                                <h1 class="font-40 color-white">Sign Up</h1>
                            </div>
                            <div class="content px-4">
                                <div class={`input-style input-transparent no-borders has-icon validate-field mb-4  ${data.username ? "input-style-active" : ""}`}>
                                    <i class="ps-1 fa fa-user"></i>
                                    <input maxLength={15} onChange={e => setData({ ...data, username: e.target.value ? e.target.value.replace(" ", "") : "" })} value={data.user} type="name" class={`form-control validate-name`} id="form1a" placeholder="Username" />
                                    <label for="form1a" class="color-highlight">Username</label>
                                    <i class="fa fa-times disabled invalid color-red-dark"></i>
                                    <i class="fa fa-check disabled valid color-green-dark"></i>
                                    <em>(required)</em>
                                </div>
                                <div class={`input-style input-transparent no-borders has-icon validate-field mb-4  ${data.password ? "input-style-active" : ""}`}>
                                    <i class="ps-1 fa fa-lock"></i>
                                    <input onChange={e => setData({ ...data, password: e.target.value })} type="password" class={`form-control validate-password`} id="form1a" placeholder="Password" />
                                    <label for="form1a" class="color-highlight">Password</label>
                                    <i class="fa fa-times disabled invalid color-red-dark"></i>
                                    <i class="fa fa-check disabled valid color-green-dark"></i>
                                    <em>(required)</em>
                                </div>
                                <div class={`input-style input-transparent no-borders has-icon validate-field mb-4  ${data.password2 ? "input-style-active" : ""}`}>
                                    <i class="ps-1 fa fa-lock"></i>
                                    <input onChange={e => setData({ ...data, password2: e.target.value })} type="password" class={`form-control validate-password`} id="form1a" placeholder="Confirm Password" />
                                    <label for="form1a" class="color-highlight">Confirm Password</label>
                                    <i class="fa fa-times disabled invalid color-red-dark"></i>
                                    <i class="fa fa-check disabled valid color-green-dark"></i>
                                    <em>(required)</em>
                                </div>
                                <div class={`input-style input-transparent no-borders has-icon validate-field mb-4  ${data.phone ? "input-style-active" : ""}`}>
                                    <i class="ps-1 fa fa-user"></i>
                                    <input onChange={e => setData({ ...data, mobile: e.target.value })} type="number" class={`form-control validate-name`} id="form1a" placeholder="Phone number" />
                                    <label for="form1a" class="color-highlight">Phone number</label>
                                    <i class="fa fa-times disabled invalid color-red-dark"></i>
                                    <i class="fa fa-check disabled valid color-green-dark"></i>
                                    <em>(required)</em>
                                </div>
                                <a href="#" data-back-button class="btn btn-full btn-l font-600 font-13 gradient-highlight mt-4 rounded-s" onClick={e => {
                                    e.preventDefault()
                                    handleSubmit()
                                }}>Sign up</a>
                                <div class="row pt-3 mb-3">
                                    <div class="col-6 text-start font-11">
                                        {/* <a class="color-white opacity-50" href="page-system-forgot-2.html">Forgot Password?</a> */}
                                    </div>
                                    <div class="col-6 text-end font-11">
                                        <a class="color-white opacity-50" href="#" onClick={e => {
                                            e.preventDefault()
                                            navigate("/")
                                        }}>Sign In</a>
                                    </div>
                                </div>
                            </div>
                        </div>
                        <div class="card-overlay bg-black opacity-85"></div>
                    </div>
                </div>
            </div>
        </div>
    )
}

export default SignUp