import React, { useContext, useEffect, useState } from 'react'
import { AppContext, UserContext } from '../App'
import Footer from '../components/Footer'
import Header from '../components/Header'
import MenuNotification from '../components/MenuNotification'
import MenuSidebar from '../components/MenuSidebar'
import { claimPromotion, getPromotions } from '../services'

function Promotions() {
    const mode = useContext(UserContext)
    const [state, setState] = useContext(AppContext)
    useEffect(() => {
        getPromotions().then(res => {
            if (res.data && Array.isArray(res.data)) {
                setState(s => ({
                    ...s,
                    promotions: res.data
                }))
            }
        }).catch(err => console.log(err))
    }, [])

    const handleClaim = (id) => {
        claimPromotion({ id }).then(res => {
            if (res.data) {
                console.log(res.data)
            }
        }).catch(err => {
            console.log(err)
        })
    }

    return (
        <div className='t1'>
            <div className='page'>
                <div class="page-content footer-clear">
                    <Header />
                    <div class="card card-fixed">
                        <div class="card-center mx-2 px-2 py-3 rounded-m">
                            <div class="row me-0 ms-0 mb-0" style={{ maxHeight: "70vh", overflowY: "scroll" }}>
                                {state.promotions.map(p => (<div class="col-12 ps-0 pe-0 mb-4">
                                    <div class="card card-style m-1 mb-0">
                                        <img src={p.image ? p.image : "images/pictures/24w.jpg"} class="img-fluid" />
                                    </div>
                                    <div className="d-flex justify-content-center">
                                        <button class="btn btn-xl gradient-highlight shadow-bg shadow-bg-s mt-4" onClick={() => handleClaim(p.id)}>Claim</button>
                                    </div>
                                </div>))}
                            </div>
                        </div>
                        {mode == "light-mode" && <div class={`card-overlay rounded-0 m-0 bg-black-light opacity-70`}></div>}
                    </div>
                </div>
                <Footer />
                <MenuNotification />
                <MenuSidebar />
            </div>
        </div>
    )
}

export default Promotions