import React, { useContext, useEffect } from 'react'
import { AppContext } from '../App'

function TopSlider() {
    const [state] = useContext(AppContext)
    useEffect(() => {
        if (window.refreshSlider) {
            window.refreshSlider()
        }
    }, [state.promotions.length])
    return (
        <div class="splide single-slider slider-no-dots slider-no-arrows slider-visible" id="single-slider-1">
            <div class="splide__track">
                <div class="splide__list">
                    {state.promotions.map(promotion => (<div class="splide__slide" style={{ width: "100vw" }}>
                        <div class={`card card-style m-0 shadow-card shadow-card-m ${!promotion.image ? "bg-5" : ""}`} style={{ height: "250px", ...promotion.image?{backgroundImage: `url(${promotion.image})`}:{} }}>
                            <div class="card-overlay"></div>
                        </div>
                    </div>))}
                </div>
            </div>
        </div>
    )
}

export default TopSlider