import React, { useContext } from 'react'
import { useNavigate } from 'react-router-dom'
import { AppContext } from '../App'

function Header() {
    const [state] = useContext(AppContext)
    const navigate = useNavigate()
    return (
        <div class="pt-3">
            <div class="page-title d-flex justify-content-end">
                <div className='align-self-center me-auto t3'>
                    <a href="#" data-bs-toggle="offcanvas" data-bs-target="#menu-sidebar" class="page-title-icon shadow-xl bg-theme color-theme" data-menu="menu-main"><i class="fa fa-lg fa-bars"></i></a>
                </div>
                <div class="align-self-center">
                    <h1>{state.wallet.username}</h1>
                    <p class="color-highlight">${state.wallet.balance}</p>
                </div>
                <div class="align-self-center">
                    <a href="#"
                        // data-bs-toggle="offcanvas"
                        // data-bs-target="#menu-notifications"
                        onClick={e => {
                            e.preventDefault()
                            navigate("/notifications")
                        }}
                        class="icon gradient-blue color-white shadow-bg shadow-bg-xs rounded-m">
                        <i class="bi bi-bell-fill font-17"></i>
                        {state.notificationCount ? <em class="badge bg-red-dark color-white scale-box">{state.notificationCount}</em> : null}
                    </a>
                </div>
            </div>
        </div>
    )
}

export default Header