import React from 'react'
import { useNavigate } from 'react-router-dom'

function Footer() {
  const navigate = useNavigate()
  return (
    <div id="footer-bar" class="footer-bar-1 footer-bar-detached">
      <a href="#" onClick={e => {
        e.preventDefault()
        navigate("/deposit")
      }}><i class="bi bi-wallet2"></i><span>Deposits</span></a>
      <a href="#" onClick={e => {
        e.preventDefault()
        navigate("/withdraw")
      }}><i class="bi bi-graph-up"></i><span>Withdraw</span></a>
      <a href="#" class="circle-nav-2" onClick={e => {
        e.preventDefault()
        navigate("/home")
      }}><i class="bi bi-house-fill"></i><span>Home</span></a>
      <a href="#" onClick={e => {
        e.preventDefault()
        navigate("/promotions")
      }}><i class="bi bi-receipt"></i><span>Promo</span></a>
      <a href="#"><i class="bi bi-three-dots"></i><span>Rebate</span></a>
    </div>
  )
}

export default Footer