import React, { useContext, useEffect, useState } from 'react'
import { AppContext } from '../App'
import { getCategoryDetail, productStart } from '../services'
import { toast } from 'react-toastify'
import Loader from './Loader'

function Tabs() {
    const [state, setState] = useContext(AppContext)
    const [active, setActive] = useState(0)
    const handleStart = (id) => {
        setState({
            ...state,
            categoryLoading: true
        })
        productStart({id}).then(res => {
            console.log(res.data)
            setState({
                ...state,
                categoryLoading: false
            })
        }).catch(err => {
            setState({
                ...state,
                categoryLoading: false
            })
            console.log(err.reponse)
        })
    }
    return (
        <div class="content t3" id="tab-group-1" style={{ margin: "20px 15px 20px 15px" }}>
            <div class="tab-controls tabs-small tabs-rounded" data-highlight="bg-highlight">
                {state.categories.map((category, index) => (
                    <a
                        href="#"
                        data-active={active == index ? true : false}
                        className={`${active == index ? "bg-highlight" : ""}`}
                        data-bs-toggle="collapse"
                        data-bs-target={`#tab-${index}`}
                        onClick={() => {
                            if (!state.categoryLoading) {
                                setState({
                                    ...state,
                                    categoryLoading: true
                                })
                                setActive(index)
                                getCategoryDetail({ category: category.name }).then(res => {
                                    if (res.data && Array.isArray(res.data)) {
                                        setState({
                                            ...state,
                                            categoriesDetails: res.data,
                                            categoryLoading: false
                                        })
                                    } else {
                                        setState({
                                            ...state,
                                            categoryLoading: false
                                        })
                                        console.log("get cateogry detail reponse", res.data)
                                        toast.error("Something went wrong")
                                    }
                                }).catch(err => {
                                    console.log(err)
                                    setState({
                                        ...state,
                                        categoryLoading: false
                                    })
                                    toast.error("Something went wrong")
                                })
                            }
                        }}
                    >
                        {category.name}
                    </a>
                ))}
            </div>
            <div class="clearfix mb-3"></div>
            {state.categories.map((category, index) => (<div data-bs-parent={`#tab-group-${index}`} class={`collapse ${(active == index) ? "show" : ""}`} id={`tab-${index}`}>
                <div class="row me-0 ms-0 mb-0 position-relative">
                    {state.categoryLoading ?
                        <Loader />
                        :
                        <>
                            {state.categoriesDetails.map((p, i) => (
                                <div class="col-3 ps-0 pe-0" onClick={()=>handleStart(p.id)}>
                                    <div class="card card-style m-1">
                                        <img src="images/pictures/25s.jpg" class="img-fluid" />
                                    </div>
                                    <div className='d-flex justify-content-center'>
                                        <span className='text-center mx-1'>{p.description}</span>
                                    </div>
                                </div>
                            ))}
                        </>}
                </div>
            </div>))}
        </div>
    )
}

export default Tabs